 ")]),_vm._v(" "),_c('p',[_vm._v("\n        Automatically Collected Information\n        In addition, the Skills may collect certain information automatically as part of normal\n        operation within the context of the Amazon Alexa or Facebook Messenger environment,\n        including, but not limited to, your hardware device unique ID, a unique user ID, your\n        prior usage of the Skills, and information about the way you use the Skills.\n        These Skills do not collect precise information about the location of your hardware device,\n         though for details of information capture by the underlying platform each relevant terms\n        and conditions agreement should be referenced.\n    ")]),_vm._v(" "),_c('p',[_vm._v("\n        Only aggregated, anonymized data is periodically transmitted to Amazon's software\n        environment and services to help us improve the Skills and our service. We will share your\n        information with third parties only in the ways that are described in this privacy\n        statement. We may disclose User Provided and Automatically Collected Information:\n        As required by law,\n        such as to comply with a subpoena, or similar legal process; When we believe\n        in good faith that disclosure is necessary to protect our rights, protect your safety or\n        the safety of others, investigate fraud, or respond to a government request; With our\n        trusted services providers who work on our behalf, do not have an independent use of\n        the information we disclose to them, and have agreed to adhere to the rules set forth\n        in this privacy statement. If Vote With Voice is involved in a merger, acquisition,\n        or sale of all or a portion of its assets, you will be notified via email and/or a\n        prominent notice on our Web site of any change in ownership or uses of this information,\n        as well as any choices you may have regarding this information.\n    ")]),_vm._v(" "),_c('p