y Policy for Skills at any time and you should check\n        this Privacy Policy for Skills periodically. Your continued use of the Service after\n        we post any modifications to the Privacy Policy for Skills on this page will constitute\n        your acknowledgment of the modifications and your consent to abide and be bound by\n        the modified Privacy Policy for Skills. If we make any material changes to this\n        privacy policy, we will notify you either through the email address you have provided us,\n        or by placing a prominent notice on our website.\n    ")]),_vm._v(" "),_c('p',[_vm._v("\n        Questions?\n        If there are any questions regarding this privacy policy, you may contact us using\n        the information below.\n\n        info@votewithvoice.com\n        Last Edited on 2018-10-14")])])])}]
